.mini-card{margin:10px;min-width:275px;min-height:100px;width:100%;border-radius:20px;background:#ecf0f3;box-sizing:border-box;transition:background 0.3s;padding:20px;box-shadow:-7px -7px 20px 0px rgba(255,255,255,0.6),-4px -4px 5px 0px rgba(255,255,255,0.6),7px 7px 20px 0px rgba(0,0,0,0.13),4px 4px 5px 0px rgba(0,0,0,0.067);box-shadow:-7px -7px 20px 0px #fff9, -4px -4px 5px 0px #fff9, 7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001}.mini-card:hover{background:#e8ecee;box-shadow:-9px -9px 20px 0px rgba(255,255,255,0.6),-6px -6px 5px 0px rgba(255,255,255,0.6),9px 9px 20px 0px rgba(0,0,0,0.13),6px 6px 5px 0px rgba(0,0,0,0.067);box-shadow:-9px -9px 20px 0px #fff9, -6px -6px 5px 0px #fff9, 9px 9px 20px 0px #0002, 6px 6px 5px 0px #0001}

.toast{visibility:hidden;min-width:200px;min-height:50px;margin-left:-100px;background:coral;text-align:center;border-radius:10px;position:fixed;z-index:1;overflow:hidden;left:50%;bottom:30px;box-shadow:-7px -7px 20px 0px rgba(255,255,255,0.6),-4px -4px 5px 0px rgba(255,255,255,0.6),7px 7px 20px 0px rgba(0,0,0,0.13),4px 4px 5px 0px rgba(0,0,0,0.067);box-shadow:-7px -7px 20px 0px #fff9, -4px -4px 5px 0px #fff9, 7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001}.toast.warn{background:gold}.toast.accent{background:deeppink}.toast.default{background:coral}.toast.show{visibility:visible;-webkit-animation:fadein 0.3s, fadeout 0.3s 2.8s;animation:fadein 0.3s, fadeout 0.3s 2.8s}.toast .frame{font-family:'Roboto', sans-serif;font-weight:200;width:190px;height:98%;padding:10px;position:relative;bottom:0;left:0;background:#ecf0f3;box-shadow:inset -7px -7px 20px 0px rgba(255,255,255,0.6),inset -4px -4px 5px 0px rgba(255,255,255,0.6),inset 4px 4px 5px 0px rgba(0,0,0,0.067);box-shadow:inset -7px -7px 20px 0px #fff9, inset -4px -4px 5px 0px #fff9, inset 4px 4px 5px 0px #0001}@-webkit-keyframes fadein{0%{bottom:0;opacity:0}80%{bottom:50px;transform:scale(0.85, 1.5)}90%{bottom:30px;opacity:1}100%{transform:scaleY(1, 1)}}@keyframes fadein{0%{bottom:0;opacity:0}80%{bottom:50px;transform:scale(0.85, 1.5)}90%{bottom:30px;opacity:1}100%{transform:scaleY(1, 1)}}@-webkit-keyframes fadeout{from{bottom:30px;opacity:1}to{bottom:0;opacity:0;transform:scale(0.85, 1.5)}}@keyframes fadeout{from{bottom:30px;opacity:1}to{bottom:0;opacity:0;transform:scale(0.85, 1.5)}}

.slider-container{width:100%}.slider-bar{background:coral;height:5px;margin-bottom:-15px}.slider-bar.shadow{margin-bottom:-5px;background:grey}.slider-value{width:50px;padding-bottom:10px;padding-left:10px}.slider{-webkit-appearance:none;width:100%;height:5px;background:transparent;outline:none;transition:opacity 0.2s;margin-right:10px}.slider::-webkit-slider-thumb:hover{background:#ff6933;background:linear-gradient(145deg, #e65f2e, #ff7037)}.slider::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;width:25px;height:25px;border-radius:50px;background:coral;background:linear-gradient(145deg, #e67248, #ff8856);cursor:pointer}.slider::-moz-range-thumb{width:25px;height:25px;border-radius:50px;background:coral;background:linear-gradient(145deg, #e67248, #ff8856);cursor:pointer}

.big-card{margin:10px;box-sizing:border-box;min-width:200px;min-height:500px;width:100%;height:100%;background:#ecf0f3;transition:background 0.3s;padding:20px;border-radius:20px;box-shadow:-7px -7px 20px 0px rgba(255,255,255,0.6),-4px -4px 5px 0px rgba(255,255,255,0.6),7px 7px 20px 0px rgba(0,0,0,0.13),4px 4px 5px 0px rgba(0,0,0,0.067);box-shadow:-7px -7px 20px 0px #fff9, -4px -4px 5px 0px #fff9, 7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001}.big-card:hover{background:#e8ecee;box-shadow:-9px -9px 20px 0px rgba(255,255,255,0.6),-6px -6px 5px 0px rgba(255,255,255,0.6),9px 9px 20px 0px rgba(0,0,0,0.13),6px 6px 5px 0px rgba(0,0,0,0.067);box-shadow:-9px -9px 20px 0px #fff9, -6px -6px 5px 0px #fff9, 9px 9px 20px 0px #0002, 6px 6px 5px 0px #0001}

